import React from "react";

//   
import { gnbData } from "../../js/data/gnb";
import { TotalMenu } from "../modules/TotalMenu";
// 
import $ from "jquery";

function TopArea(props) {

  //     ////
  const showHideMenu = (e)=>{
    // console.log(e.currentTarget);

    //   : .mbox
    // 1.    
    $(".mbox").fadeToggle(300);
    // fadeIn()  
    // fadeOut()   -> display:none
    // fadeToggle()  / 

    // 2.    "on"/
    $(e.currentTarget).toggleClass("on");
    // addClass() 
    // removeClass() 
    // toggleClass() /

    // console.log($(e.currentTarget).is(".on"));

    // 3.  /
    // : .bgm
    let bgm = $(".bgm");
    //     
    // get(0)   !

    //  "on"    
    $(e.currentTarget).is(".on")?
    bgm.get(0).play()
    : bgm.get(0).pause();
    // play() , pause()  

    //  JS   play(), pause() 
    // document.querySelector(".bgm").play();
    // document.querySelector(".bgm").pause();

  }; ////////// showHideMenu /////////////

  //   //////////////
  return (
    <>
      <div id="top-area">
        <header className="top-area ibx">
          <h1 id="logo">
            <a href="#">
              <img src="./images/main_logo.png" alt="" />
            </a>
          </h1>
          <nav className="gnb">
            <ul>
              <li className="bld"> li </li>
              {gnbData.main.map((v, i) => (
                <li key={i}>
                  <a href="#">{v}</a>
                </li>
              ))}
            </ul>
          </nav>
          <div className="ham" onClick={showHideMenu}>
            <span></span> <span></span> <span></span>
          </div>
          {/*   */}
          <TotalMenu />
        </header>
      </div>
    </>
  );
}

export default TopArea;
