import React, { useContext, useEffect, useLayoutEffect, useState } from "react";

//   JS
import { scrolled, setPos } from "../../js/func/smoothScroll24";

//  API 
import { pCon } from "../modules/pCon";

//  
import $ from "jquery";

// CSS
import "../../css/fashion.scss";
import { SwiperBan } from "../plugin/SwiperBan";
import SinSang from "../modules/SinSang";

function Fashion({subCat}) {
    // subCat -  
    // : men / women / style

  //  API
  const myCon = useContext(pCon);

  // ((   : useLayoutEffect ))
  // DOM   DOM 
  //    useLayoutEffect!
  //   DOM   !
  useLayoutEffect(() => {
    document.addEventListener("wheel", scrolled, { passive: false });
    //   passive:false  
    //   true  window,document,body
    //   preventDefault()   
    //       treu!
    // passive false  window,document,body
    //     !(  !)

    //   
    // setPos(0);

    //   
    window.scrollTo(0, 0);

    //  (x )
    $("html,body").css({
      overflow: "visible",
      overflowX: "hidden",
    });

    //   //////////
    return () => {
      //   
      document.removeEventListener("wheel", scrolled, { passive: false });

      //  
      $("html,body").css({
        overflow: "hidden",
      });

      //   
      setPos(0);

      //   
      window.scrollTo(0, 0);
    };
  }, []);

  // ((   : useEffect ))
  // ->     ////
  // DOM    !
  useEffect(() => {
    //    
    $("#logo a").on("click", (e) => {
      e.preventDefault();
      myCon.setPgName("main");
    }); ////////// click ////////////
  }, []);

  

  //  
  const [item, setItem] = useState("m1");

  //     
  //      !
  //  ~!!
  const chgItem = (v) => {
    console.log(":", v);
    //  
    setItem(v);
    //    
    $(".bgbx").slideDown(400);
  }; /////////// chgItem  //////

  //  //////////////////
  return (
    <>
      {/* 1.  */}
      <section id="ban" className="page">
        <SwiperBan cat={subCat} />
      </section>
      {/* 2.  */}
      <section id="c1" className="cont sc-ani c1">
        <SinSang cat={subCat} chgItemFn={chgItem} />
      </section>
      {/* 2.5.  */}
      <div className="bgbx"></div>
      {/* 3.   :    */}
      <section id="c2" className="cont"></section>
      {/* 4.  */}
      <section id="c3" className="cont c3"></section>
      {/* 5.  */}
      <section id="c4" className="cont c4"></section>
    </>
  );
}

export default Fashion;
