//    ////
import React, { useContext } from "react";

//  API 
import { pCon } from "./pCon";

//  
import { fsData } from "../../js/data/fashion_intro";

// CSS
import "../../css/fashion_intro.scss";

function FashionIntro({ catName, subCat, opt }) {
  // 1. catName -  (men/women/style)
  // 2. subCat -  
  //  (  subCat  "etc"!)
  // 3. opt - ( true /  false)
  //  ( flex-direction: row-reverse !)

  //  API
  const myCon = useContext(pCon);

  //   
  const selData = fsData[catName];

  return (
    <div id={catName} className="fs-page">
      <ul
        className="pgc"
        style={{
          // ,  
          flexDirection: opt ? "row-reverse" : "row",
        }}
      >
        {/* 1.    */}
        <li className="imgc">
          <img src={selData.isrc[0]} alt={selData.ialt[0]} />
        </li>
        {/* 2.    */}
        <li className="txtc">
          <h2 className={catName == "style" ? "tm" : ""}>
            {/* ()     html 
            dangerouslySetInnerHTML={{__html:}} 
             ! */}
            <a href="#" onClick={(e)=>{
              e.preventDefault();
              myCon.setPgName(catName);
            }}>
              {selData.tit[0][0]}
              <br />
              {selData.tit[0][1]}
            </a>
          </h2>
          {
            //     
            catName == "style" && (
              <h2 className="tw">
                <a href="#">
                  {selData.tit[1][0]}
                  <br />
                  {selData.tit[1][1]}
                </a>
              </h2>
            )
          }
        </li>
        {/* 3.   : ! */}
        {
          //   li  
          catName == "style" && (
            <li className="imgc">
              <img 
                src={selData.isrc[1]} 
                alt={selData.ialt[1]} />
            </li>
          )
        }
      </ul>
    </div>
  );
}

export default FashionIntro;
