import React from "react";
import MainCont from "../pages/MainCont";
import Fashion from "../pages/Fashion";

function MainArea({ page }) {
  // page  
  // 5  : main / glist / men / women / style
  console.log("page:",page);

  //   /////////////
  return (
    <>
      {/*    */}
      {page == "main" && <MainCont />}
      {
      (page == "men" ||
      page == "women" ||
      page == "style") 
      
      && <Fashion subCat={page} />}
    </>
  );
}

export default MainArea;
