import $ from "jquery";

//      /////
const showInfo = (e,selData) => {
  e.preventDefault();
  // 
  const tg = $(e.currentTarget);
  // 1.   li class( )
  let gKey = tg.attr("class");
  // console.log('!',selData[gKey]);

  // 2.   
  //   li 
  tg.append(`<div class="ibox"></div>`);

  // console.log(
  //   selData[gKey].split('^')
  //   .map((v)=>`<div>${v}</div>`));

  // 3. li  .ibox  +
  tg.find(".ibox")
    .html(
      selData[gKey]
        .split("^")
        .map((v, i) => `<div>${i == 2 ? addComma(v) + "" : v}</div>`)
    )
    // 
    .animate(
      {
        top: "110%",
        opacity: 1,
        zIndex: 1,
      },
      300
    );
}; /////////// showInfo ///////////////

//(   )
function addComma(x) {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

//   
const removeInfo = (e) => {
  e.preventDefault();
  $(e.currentTarget).find(".ibox").remove();
};

// [    ] //////
const flowList = (ele,lpos,callSts) => {
  // ele-
  // console.log(ele);
  //  left 1 
  lpos.current--;

  //      
  if (lpos.current < -300) {
    //  !(-301 0 !)
    lpos.current = 0;
    //  li  
    ele.append(ele.find("li").first());
  } ///// if //////

  // 
  ele.css({ left: lpos.current + "px" });

  // 
  if (callSts.current) 
  setTimeout(() => flowList(ele,lpos,callSts), 40);
}; ////////// flowList ////////////

//  /////
export {showInfo,removeInfo,flowList};
