//   

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

import "./css/swiper_ban.scss";

// import required modules
//    
// ( ,,)
import { Pagination, Navigation, Autoplay } from "swiper/modules";
import { useEffect, useRef, useState } from "react";

export function SwiperBan({ cat }) {
  // cat - 
  console.log(":", cat);

  //    
  const swpObj = useRef(null);

  //   ///////////
  useEffect(() => {
    //    
    //  
    let objSwp = swpObj.current.swiper;
    //   : slideTo(,)
    objSwp.slideTo(0, 0);
    //   0,  0 

    //  ?
    console.log(":", swpObj);
    console.log("Swiper:", swpObj.current.swiper);
    //     
    // ref useRef  
    //   ! ->  !!!
    // :
    // (1)   : .current
    // (2)   : .current.swiper

    // ) ref useRef  
    //      !!!
  }); ////// useEffect //////////////

  //   ////
  const makeList = (num) => {
    // num
    let temp = [];
    for (let x = 0; x < num; x++) {
      temp[x] = (
        <SwiperSlide key={x}>
          {(cat == "men" || cat == "women") && x == 0 ? (
            <video
              src={"./images/sub/" + cat + "/banner/mv.mp4"}
              style={{ width: "100%", height: "100%", objectFit: "cover" }}
              muted
              // loop ->  
              className={cat + "-vid"}
              //   autoPlay
            />
          ) : (
            <img
              src={"./images/sub/" + cat + "/banner/ban" + (x + 1) + ".png"}
            />
          )}
        </SwiperSlide>
      );
    } /////////// for /////////////

    //  
    return temp;
  }; ///////////// makeList  //////////

  //  ///////////////////
  return (
    <>
      <Swiper
        ref={swpObj}
        /* ref  useRef   
          */
        onInit={(swp) => {
          console.log(" !", swp);
        }}
        slidesPerView={1}
        spaceBetween={0}
        pagination={{
          clickable: true,
        }}
        // ->   !
        // autoplay={{
        //   delay: 3000,
        //   disableOnInteraction: false,
        // }}
        loop={true}
        navigation={true}
        /*     */
        modules={[Pagination, Navigation, Autoplay]}
        className="mySwiper"
        //   
        onSlideChange={(swp) => {
          // swp    
          // activeIndex loop 
          // realIndex loop  !

          // style   
          if (cat == "style"){ 
            //  
            swp.autoplay.start();
            //   true!
            swp.autoplay.running = true;
            //  
            return;
          } ///// if ////

          //   //
          let mvEle = document.querySelector(`.${cat}-vid`);

          //  
          let idx = swp.realIndex;
          console.log(":", idx);

          // men / women   
          if (idx == 0) {
            //   
            swp.autoplay.stop();
            swp.autoplay.running = false;

            // 
            mvEle.play();

            //    
            // timeupdate :  
            mvEle.addEventListener("timeupdate", (e) => {
              //    true
              //  -> paused
              console.log("~!!!", e.target.paused);
              //    
              if (e.target.paused) {
                //  
                swp.slideNext();
                //  
                swp.autoplay.start();
                //   true!
                swp.autoplay.running = true;
              } ///// if ////////
            }); ///////// timeupdate /////////
          } /// if ///
          //   
          else {
            mvEle.pause();
          } /// else ///
        }}
      >
        {makeList(cat == "style" ? 5 : 3)}
      </Swiper>
    </>
  );
} /////////// SwiperBan  ///////////
