import React, { useLayoutEffect, useRef } from "react";

//  
import $ from "jquery";

//    ////
import { showInfo, removeInfo, flowList } from "../../js/func/sinsang_fn";

//    /////
import { sinsangData } from "../../js/data/sinsang";

function SinSang({ cat, chgItemFn }) {
  // cat -   (men/women/style)
  // chgItemFn -   

  //     ///
  // (left) ->   
  // ->  useRef !! -> .current !
  const lpos = useRef(0);
  //  (1-,0-)
  const callSts = useRef(1);

  //  cat    !
  useLayoutEffect(()=>{
    //    
    lpos.current = 0;
    //  /  
    callSts.current = 1;
  },[cat]); /////// cat 

  //    
  const selData = sinsangData[cat];

  // [   ] //////////
  const makeList = () => {
    //   
    let temp = [];
    //    for  JSX 
    for (let x = 0; x < 9; x++) {
      temp[x] = (
        <li
          className={"m" + (x + 1)}
          key={x}
          onMouseEnter={(e) => showInfo(e, selData)}
          onMouseLeave={removeInfo}
        >
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              chgItemFn("m" + (x + 1));
            }}
          >
            <img
              src={"./images/goods/" + cat + "/m" + (x + 1) + ".png"}
              alt=""
            />
          </a>
        </li>
      );
    } ///// for /////
    // JSX   ->!
    return temp;
  };

  //  /////////////////////
  return (
    <>
      <h2 className="c1tit">
        NEW MEN'S ARRIVAL
        <button></button>
      </h2>
      <div
        className="flowbx"
        onMouseEnter={() => {}}
        onMouseLeave={() => {
          // callSts.current = 1;
          flowList($(".flist"), lpos, callSts);
        }}
      >
        <ul className="flist">{makeList()}</ul>
      </div>
    </>
  );
}

export default SinSang;
